import * as React from 'react';
export class MailboxComponent extends React.Component<any, any> {
  render() {
    return (
      <div className="inner-box">
        <h2 className="title-2"><i className="icon-mail"></i> Hm th c nhn </h2>
        <div className="table-responsive">
          <div className="table-action">
            <div className="btn-group">
              <button type="button" className="btn btn-xs btn-primary dropdown-toggle" data-toggle="dropdown" aria-haspopup="true"
                      aria-expanded="false">
                nh du  <i className="caret"/>
              </button>
              <ul className="dropdown-menu">
                <li><a href="#"> c</a></li>
                <li><a href="#">Cha c</a></li>
                <li role="separator" className="divider"/>
                <li><a href="#">Spam</a></li>
              </ul>
            </div>
            <a href="#" className="btn btn-xs btn-primary"><i className="glyphicon glyphicon-remove "/> Xa th</a>
          </div>
          <table className="table table-hover mailbox-table table demo footable-loaded footable"
                 data-filter="#filter" data-filter-text-only="true">
            <thead>
            <tr>
              <th data-type="numeric" data-sort-initial="true"></th>
              <th> Ngi gi</th>
              <th data-sort-ignore="true"> Ni dung</th>
              <th data-type="numeric"> Ngy gi</th>
            </tr>
            </thead>
            <tbody>
            {emails.map((email, x) => {
              return (
                <tr key={x} className={`${email.isRead ? 'active' : 'unread'}`}>
                  <td style={{width: '2%'}}>
                    <label>
                      <input type="checkbox"/>
                    </label>
                  </td>
                  <td style={{width: '14%'}}>
                    <span>{email.from}</span>
                  </td>
                  <td style={{width: '58%'}}>
                    <span>{email.title}</span>
                  </td>
                  <td style={{width: '16%'}}>
                    <div><span>{email.sentDate}</span></div>
                  </td>
                </tr>
              );
            })}
            </tbody>
          </table>
        </div>

      </div>
    )
  }
}

